.section-white1 {
  background: #fff;
  padding: 70px 0;
}

 

.team-item1{
  background: #f4f5f6;
  text-align:center;
  margin:20px 0;
  padding:50px 20px 40px 20px;
  border-radius: 8px 8px;
  -webkit-transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -ms-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

 


.team-item1:hover{
  background: #3956de;
  color: white;
}

 

.team-item1:hover{
  background: #39de78;
  color: white;
}

 

.team-item1 h3{
  margin:20px 0 1px 0!important;
  color:#454545;
  font-size:40px;
  font-weight: 700;
  text-transform:uppercase;
  transition: all .25s ease-in-out;
  -moz-transition: all .25s ease-in-out;
  -webkit-transition: all .25s ease-in-out;
}

 

 

.section-subtitle1 {
  white-space: pre-line;
 } 

.team-item1:hover h3{
  color:#ddd;
}

 

.team-info1 {
  display: block;
  margin-bottom:0;
}

 


.team-info1 p{
  color:#acacac;
  font-style:italic;
}


 


.team-info1::after {
    background: #008aff;
    background: -webkit-linear-gradient(135deg, #6B02FF 0%, #985BEF 100%);
    background: -o-linear-gradient(bottom right, #6B02FF, #985BEF);
    background: -moz-linear-gradient(bottom right, #008aff, #1ad2fd);
    background: linear-gradient(135deg, #6B02FF 0%, #985BEF 100%);
    display: inline-block;
    vertical-align: middle;
    content: "";
    width: 350px;
    height: 3px; 
}

 

 .team-img1{
  max-width: 140px;
  padding: 3px 3px;
    background-color: #dedede;
  
}
 
 

 
 
